import { createSlice } from '@reduxjs/toolkit';

const initialState = {
   GreateProfileData: null,
  errors:[]
};

export const GreateProfileSlice = createSlice({
    name: 'GreateProfileSlice',
    initialState,
    reducers: {
      GreateProfileSliceInfo: (state, action) => {
        // Redux Toolkit allows us to write "mutating" logic in reducers. It
        // doesn't actually mutate the state because it uses the Immer library,
        // which detects changes to a "draft state" and produces a brand new
        // immutable state based off those changes
        state.GreateProfileData = action.payload;
        console.log(state.GreateProfileData);
      },
      errors: (state, action) => {
          // Redux Toolkit allows us to write "mutating" logic in reducers. It
          // doesn't actually mutate the state because it uses the Immer library,
          // which detects changes to a "draft state" and produces a brand new
          // immutable state based off those changes
          state.errors = action.payload;
        },
    
    }
  });

  // Action creators are generated for each case reducer function
export const { GreateProfileSliceInfo ,errors} = GreateProfileSlice.actions;

export default GreateProfileSlice.reducer;